"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { X, Plus } from "lucide-react";

interface Language {
  id: string;
  name: string;
  fluency: string;
}

// List of languages (ISO 639-1)
const languages = [
  "English", "Filipino", "Cebuano", "Ilocano", "Hiligaynon", "Waray", "Kapampangan",
  "Arabic", "Bengali", "Chinese", "French", "German", "Hindi", "Indonesian",
  "Italian", "Japanese", "Korean", "Malay", "Portuguese", "Russian", "Spanish",
  "Thai", "Turkish", "Vietnamese"
].sort();

// CEFR Fluency Levels
const fluencyLevels = [
  { value: "A1", label: "A1 - Beginner" },
  { value: "A2", label: "A2 - Elementary" },
  { value: "B1", label: "B1 - Intermediate" },
  { value: "B2", label: "B2 - Upper Intermediate" },
  { value: "C1", label: "C1 - Advanced" },
  { value: "C2", label: "C2 - Mastery" }
];

export default function LanguagesSection() {
  const [userLanguages, setUserLanguages] = useState<Language[]>([]);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});

  const addLanguage = () => {
    const newLanguage: Language = {
      id: Date.now().toString(),
      name: "",
      fluency: "",
    };
    setUserLanguages([...userLanguages, newLanguage]);
  };

  const removeLanguage = (id: string) => {
    setUserLanguages(userLanguages.filter(lang => lang.id !== id));
  };

  const updateLanguage = (id: string, field: keyof Language, value: string) => {
    setUserLanguages(userLanguages.map(lang => {
      if (lang.id === id) {
        // Check for duplicate language
        if (field === 'name' && userLanguages.some(l => l.id !== id && l.name === value)) {
          setErrors(prev => ({ ...prev, [`${id}-name`]: "This language is already added" }));
          return lang;
        }
        setErrors(prev => ({ ...prev, [`${id}-${field}`]: "" }));
        return { ...lang, [field]: value };
      }
      return lang;
    }));
  };

  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};
    let isValid = true;

    userLanguages.forEach(lang => {
      if (!lang.name) {
        newErrors[`${lang.id}-name`] = "Language is required";
        isValid = false;
      }
      if (!lang.fluency) {
        newErrors[`${lang.id}-fluency`] = "Fluency level is required";
        isValid = false;
      }
    });

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      console.log("Saving languages:", userLanguages);
      // Handle form submission
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Languages</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {userLanguages.map((language) => (
            <Card key={language.id} className="p-4 relative">
              <button
                type="button"
                onClick={() => removeLanguage(language.id)}
                className="absolute top-2 right-2 text-gray-500 hover:text-red-500"
              >
                <X className="h-5 w-5" />
              </button>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label>Language *</Label>
                  <Select
                    value={language.name}
                    onValueChange={(value) => updateLanguage(language.id, "name", value)}
                  >
                    <SelectTrigger className={errors[`${language.id}-name`] ? "border-red-500" : ""}>
                      <SelectValue placeholder="Select language" />
                    </SelectTrigger>
                    <SelectContent>
                      {languages.map((lang) => (
                        <SelectItem key={lang} value={lang}>
                          {lang}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors[`${language.id}-name`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${language.id}-name`]}</p>
                  )}
                </div>

                <div>
                  <Label>Fluency Level *</Label>
                  <Select
                    value={language.fluency}
                    onValueChange={(value) => updateLanguage(language.id, "fluency", value)}
                  >
                    <SelectTrigger className={errors[`${language.id}-fluency`] ? "border-red-500" : ""}>
                      <SelectValue placeholder="Select fluency level" />
                    </SelectTrigger>
                    <SelectContent>
                      {fluencyLevels.map((level) => (
                        <SelectItem key={level.value} value={level.value}>
                          {level.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors[`${language.id}-fluency`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${language.id}-fluency`]}</p>
                  )}
                </div>
              </div>
            </Card>
          ))}

          <div className="flex justify-between">
            <Button type="button" variant="outline" onClick={addLanguage}>
              <Plus className="h-4 w-4 mr-1" /> Add Language
            </Button>
            <Button type="submit" disabled={userLanguages.length === 0}>
              Save Languages
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
