"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { X, Plus, Image as ImageIcon } from "lucide-react";
import Image from "next/image";

interface ProjectImage {
  id: string;
  url: string;
}

interface Project {
  id: string;
  title: string;
  description: string;
  technologies: string[];
  images: ProjectImage[];
}

export default function ProjectsForm() {
  const [projects, setProjects] = useState<Project[]>([]);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});
  const [newTechnology, setNewTechnology] = useState<string>("");

  const addProject = () => {
    const newProject: Project = {
      id: Date.now().toString(),
      title: "",
      description: "",
      technologies: [],
      images: [],
    };
    setProjects([...projects, newProject]);
  };

  const removeProject = (id: string) => {
    setProjects(projects.filter(project => project.id !== id));
  };

  const updateProject = (id: string, field: keyof Project, value: any) => {
    setProjects(projects.map(project => {
      if (project.id === id) {
        return { ...project, [field]: value };
      }
      return project;
    }));
    // Clear error when field is updated
    setErrors(prev => ({ ...prev, [`${id}-${field}`]: "" }));
  };

  const addTechnology = (projectId: string) => {
    if (!newTechnology.trim()) return;

    setProjects(projects.map(project => {
      if (project.id === projectId) {
        if (project.technologies.includes(newTechnology.trim())) {
          setErrors(prev => ({
            ...prev,
            [`${projectId}-technology`]: "Technology already exists"
          }));
          return project;
        }
        return {
          ...project,
          technologies: [...project.technologies, newTechnology.trim()]
        };
      }
      return project;
    }));
    setNewTechnology("");
    setErrors(prev => ({ ...prev, [`${projectId}-technology`]: "" }));
  };

  const removeTechnology = (projectId: string, technology: string) => {
    setProjects(projects.map(project => {
      if (project.id === projectId) {
        return {
          ...project,
          technologies: project.technologies.filter(tech => tech !== technology)
        };
      }
      return project;
    }));
  };

  const handleImageUpload = (projectId: string, files: FileList | null) => {
    if (!files) return;

    const newImages: ProjectImage[] = Array.from(files).map(file => ({
      id: Date.now().toString(),
      url: URL.createObjectURL(file)
    }));

    setProjects(projects.map(project => {
      if (project.id === projectId) {
        return {
          ...project,
          images: [...project.images, ...newImages]
        };
      }
      return project;
    }));
  };

  const removeImage = (projectId: string, imageId: string) => {
    setProjects(projects.map(project => {
      if (project.id === projectId) {
        return {
          ...project,
          images: project.images.filter(img => img.id !== imageId)
        };
      }
      return project;
    }));
  };

  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};
    let isValid = true;

    projects.forEach(project => {
      if (!project.title.trim()) {
        newErrors[`${project.id}-title`] = "Project title is required";
        isValid = false;
      }

      if (!project.description.trim()) {
        newErrors[`${project.id}-description`] = "Project description is required";
        isValid = false;
      }

      if (project.technologies.length === 0) {
        newErrors[`${project.id}-technologies`] = "At least one technology is required";
        isValid = false;
      }
    });

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      console.log("Saving projects:", projects);
      // Handle form submission
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Projects</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {projects.map((project) => (
            <Card key={project.id} className="p-4 relative">
              <button
                type="button"
                onClick={() => removeProject(project.id)}
                className="absolute top-2 right-2 text-gray-500 hover:text-red-500"
              >
                <X className="h-5 w-5" />
              </button>

              <div className="space-y-4">
                <div>
                  <Label htmlFor={`title-${project.id}`}>Project Title *</Label>
                  <Input
                    id={`title-${project.id}`}
                    value={project.title}
                    onChange={(e) => updateProject(project.id, "title", e.target.value)}
                    className={errors[`${project.id}-title`] ? "border-red-500" : ""}
                  />
                  {errors[`${project.id}-title`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${project.id}-title`]}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`description-${project.id}`}>Description *</Label>
                  <Textarea
                    id={`description-${project.id}`}
                    value={project.description}
                    onChange={(e) => updateProject(project.id, "description", e.target.value)}
                    className={errors[`${project.id}-description`] ? "border-red-500" : ""}
                  />
                  {errors[`${project.id}-description`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${project.id}-description`]}</p>
                  )}
                </div>

                <div>
                  <Label>Technologies Used *</Label>
                  <div className="flex gap-2 mt-1">
                    <Input
                      value={newTechnology}
                      onChange={(e) => setNewTechnology(e.target.value)}
                      placeholder="Add a technology"
                      onKeyPress={(e) => {
                        if (e.key === 'Enter') {
                          e.preventDefault();
                          addTechnology(project.id);
                        }
                      }}
                    />
                    <Button type="button" onClick={() => addTechnology(project.id)}>
                      Add
                    </Button>
                  </div>
                  {errors[`${project.id}-technology`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${project.id}-technology`]}</p>
                  )}
                  {errors[`${project.id}-technologies`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${project.id}-technologies`]}</p>
                  )}
                  <div className="flex flex-wrap gap-2 mt-2">
                    {project.technologies.map((tech, index) => (
                      <div
                        key={index}
                        className="flex items-center gap-1 bg-gray-100 rounded-full px-3 py-1"
                      >
                        <span>{tech}</span>
                        <button
                          type="button"
                          onClick={() => removeTechnology(project.id, tech)}
                          className="text-gray-500 hover:text-red-500"
                        >
                          <X className="h-4 w-4" />
                        </button>
                      </div>
                    ))}
                  </div>
                </div>

                <div>
                  <Label>Screenshots/Pictures (Optional)</Label>
                  <div className="mt-2">
                    <Input
                      type="file"
                      accept="image/*"
                      multiple
                      onChange={(e) => handleImageUpload(project.id, e.target.files)}
                    />
                  </div>
                  <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mt-4">
                    {project.images.map((image) => (
                      <div key={image.id} className="relative group">
                        <div className="aspect-video relative rounded-lg overflow-hidden">
                          <Image
                            src={image.url}
                            alt="Project screenshot"
                            fill
                            className="object-cover"
                          />
                        </div>
                        <button
                          type="button"
                          onClick={() => removeImage(project.id, image.id)}
                          className="absolute top-2 right-2 bg-red-500 text-white rounded-full p-1 opacity-0 group-hover:opacity-100 transition-opacity"
                        >
                          <X className="h-4 w-4" />
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </Card>
          ))}

          <div className="flex justify-between">
            <Button type="button" variant="outline" onClick={addProject}>
              <Plus className="h-4 w-4 mr-1" /> Add Project
            </Button>
            <Button type="submit" disabled={projects.length === 0}>
              Save Projects
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
