"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Facebook, Github, Linkedin, Twitter } from "lucide-react";

interface SocialLinks {
  linkedin: string;
  facebook: string;
  github: string;
  twitter: string;
}

interface Errors {
  [key: string]: string;
}

export default function SocialMediaLinksForm() {
  const [socialLinks, setSocialLinks] = useState<SocialLinks>({
    linkedin: "",
    facebook: "",
    github: "",
    twitter: "",
  });

  const [errors, setErrors] = useState<Errors>({});

  const validateUrl = (url: string, platform: string) => {
    if (!url) return true; // Empty URLs are allowed (optional fields)

    const patterns = {
      linkedin: /^https?:\/\/(www\.)?linkedin\.com\/in\/[\w-]+\/?$/,
      facebook: /^https?:\/\/(www\.)?facebook\.com\/[\w.]+\/?$/,
      github: /^https?:\/\/(www\.)?github\.com\/[\w-]+\/?$/,
      twitter: /^https?:\/\/(www\.)?twitter\.com\/[\w]+\/?$/,
    };

    return patterns[platform as keyof typeof patterns].test(url);
  };

  const handleChange = (platform: keyof SocialLinks, value: string) => {
    setSocialLinks(prev => ({
      ...prev,
      [platform]: value
    }));

    // Clear error when user starts typing
    if (errors[platform]) {
      setErrors(prev => ({
        ...prev,
        [platform]: ""
      }));
    }
  };

  const validateForm = () => {
    const newErrors: Errors = {};
    let isValid = true;

    // Validate each URL if provided
    (Object.keys(socialLinks) as Array<keyof SocialLinks>).forEach(platform => {
      const url = socialLinks[platform];
      if (url && !validateUrl(url, platform)) {
        newErrors[platform] = `Invalid ${platform} URL format`;
        isValid = false;
      }
    });

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      console.log("Saving social media links:", socialLinks);
      // Handle form submission
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Social Media Links</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-4">
            {/* LinkedIn */}
            <div>
              <Label htmlFor="linkedin" className="flex items-center gap-2">
                <Linkedin className="h-5 w-5" />
                LinkedIn Profile
              </Label>
              <div className="mt-1">
                <Input
                  id="linkedin"
                  type="url"
                  placeholder="https://linkedin.com/in/username"
                  value={socialLinks.linkedin}
                  onChange={(e) => handleChange("linkedin", e.target.value)}
                  className={errors.linkedin ? "border-red-500" : ""}
                />
                {errors.linkedin && (
                  <p className="text-red-500 text-sm mt-1">{errors.linkedin}</p>
                )}
              </div>
            </div>

            {/* Facebook */}
            <div>
              <Label htmlFor="facebook" className="flex items-center gap-2">
                <Facebook className="h-5 w-5" />
                Facebook Profile
              </Label>
              <div className="mt-1">
                <Input
                  id="facebook"
                  type="url"
                  placeholder="https://facebook.com/username"
                  value={socialLinks.facebook}
                  onChange={(e) => handleChange("facebook", e.target.value)}
                  className={errors.facebook ? "border-red-500" : ""}
                />
                {errors.facebook && (
                  <p className="text-red-500 text-sm mt-1">{errors.facebook}</p>
                )}
              </div>
            </div>

            {/* GitHub */}
            <div>
              <Label htmlFor="github" className="flex items-center gap-2">
                <Github className="h-5 w-5" />
                GitHub Profile
              </Label>
              <div className="mt-1">
                <Input
                  id="github"
                  type="url"
                  placeholder="https://github.com/username"
                  value={socialLinks.github}
                  onChange={(e) => handleChange("github", e.target.value)}
                  className={errors.github ? "border-red-500" : ""}
                />
                {errors.github && (
                  <p className="text-red-500 text-sm mt-1">{errors.github}</p>
                )}
              </div>
            </div>

            {/* Twitter/X */}
            <div>
              <Label htmlFor="twitter" className="flex items-center gap-2">
                <Twitter className="h-5 w-5" />
                X (Twitter) Profile
              </Label>
              <div className="mt-1">
                <Input
                  id="twitter"
                  type="url"
                  placeholder="https://twitter.com/username"
                  value={socialLinks.twitter}
                  onChange={(e) => handleChange("twitter", e.target.value)}
                  className={errors.twitter ? "border-red-500" : ""}
                />
                {errors.twitter && (
                  <p className="text-red-500 text-sm mt-1">{errors.twitter}</p>
                )}
              </div>
            </div>
          </div>

          <div className="flex justify-end">
            <Button type="submit">Save Social Media Links</Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
