"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";

export default function ProfessionalSummaryForm() {
  const [summary, setSummary] = useState("");
  const [isGenerating, setIsGenerating] = useState(false);
  const [error, setError] = useState("");

  const generateAISummary = async () => {
    setIsGenerating(true);
    setError("");
    
    try {
      // Simulating AI generation with a timeout
      await new Promise((resolve) => setTimeout(resolve, 1500));
      
      // Example generated summary
      const generatedSummary = `Experienced professional with a proven track record of success in developing innovative solutions and driving business growth. Skilled in project management, team leadership, and strategic planning. Demonstrated ability to collaborate effectively with cross-functional teams and deliver results in fast-paced environments.`;
      
      setSummary(generatedSummary);
    } catch (err) {
      setError("Failed to generate summary. Please try again.");
    } finally {
      setIsGenerating(false);
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (summary.trim()) {
      console.log("Saving professional summary:", summary);
      // Handle form submission
    } else {
      setError("Please generate or enter a professional summary");
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Professional Summary</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <Label htmlFor="summary">Professional Summary</Label>
            <div className="mt-2">
              <Textarea
                id="summary"
                value={summary}
                onChange={(e) => setSummary(e.target.value)}
                placeholder="Your professional summary will appear here..."
                className="min-h-[200px]"
              />
            </div>
            {error && <p className="text-red-500 text-sm mt-1">{error}</p>}
          </div>

          <div className="flex gap-4">
            <Button
              type="button"
              variant="outline"
              onClick={generateAISummary}
              disabled={isGenerating}
            >
              {isGenerating ? "Generating..." : "Generate AI Summary"}
            </Button>
            <Button type="submit">Save Summary</Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
