"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { X, Plus } from "lucide-react";

interface Certification {
  id: string;
  name: string;
  issuingOrganization: string;
  dateReceived: string;
}

export default function CertificationForm() {
  const [certifications, setCertifications] = useState<Certification[]>([]);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});

  const addCertification = () => {
    const newCertification: Certification = {
      id: Date.now().toString(),
      name: "",
      issuingOrganization: "",
      dateReceived: "",
    };
    setCertifications([...certifications, newCertification]);
  };

  const removeCertification = (id: string) => {
    setCertifications(certifications.filter(cert => cert.id !== id));
  };

  const updateCertification = (id: string, field: keyof Certification, value: string) => {
    setCertifications(certifications.map(cert => {
      if (cert.id === id) {
        return { ...cert, [field]: value };
      }
      return cert;
    }));
    // Clear error when field is updated
    setErrors(prev => ({ ...prev, [`${id}-${field}`]: "" }));
  };

  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};
    let isValid = true;
    const currentDate = new Date();

    certifications.forEach(cert => {
      if (!cert.name.trim()) {
        newErrors[`${cert.id}-name`] = "Certification name is required";
        isValid = false;
      }

      if (!cert.issuingOrganization.trim()) {
        newErrors[`${cert.id}-issuingOrganization`] = "Issuing organization is required";
        isValid = false;
      }

      if (!cert.dateReceived) {
        newErrors[`${cert.id}-dateReceived`] = "Date received is required";
        isValid = false;
      } else {
        const certDate = new Date(cert.dateReceived);
        if (certDate > currentDate) {
          newErrors[`${cert.id}-dateReceived`] = "Date cannot be in the future";
          isValid = false;
        }
      }
    });

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      console.log("Saving certifications:", certifications);
      // Handle form submission
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Certifications</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {certifications.map((certification) => (
            <Card key={certification.id} className="p-4 relative">
              <button
                type="button"
                onClick={() => removeCertification(certification.id)}
                className="absolute top-2 right-2 text-gray-500 hover:text-red-500"
              >
                <X className="h-5 w-5" />
              </button>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor={`name-${certification.id}`}>Certification Name *</Label>
                  <Input
                    id={`name-${certification.id}`}
                    value={certification.name}
                    onChange={(e) => updateCertification(certification.id, "name", e.target.value)}
                    className={errors[`${certification.id}-name`] ? "border-red-500" : ""}
                  />
                  {errors[`${certification.id}-name`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${certification.id}-name`]}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`issuingOrganization-${certification.id}`}>Issuing Organization *</Label>
                  <Input
                    id={`issuingOrganization-${certification.id}`}
                    value={certification.issuingOrganization}
                    onChange={(e) => updateCertification(certification.id, "issuingOrganization", e.target.value)}
                    className={errors[`${certification.id}-issuingOrganization`] ? "border-red-500" : ""}
                  />
                  {errors[`${certification.id}-issuingOrganization`] && (
                    <p className="text-red-500 text-sm mt-1">
                      {errors[`${certification.id}-issuingOrganization`]}
                    </p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`dateReceived-${certification.id}`}>Date Received *</Label>
                  <Input
                    id={`dateReceived-${certification.id}`}
                    type="date"
                    value={certification.dateReceived}
                    onChange={(e) => updateCertification(certification.id, "dateReceived", e.target.value)}
                    max={new Date().toISOString().split("T")[0]}
                    className={errors[`${certification.id}-dateReceived`] ? "border-red-500" : ""}
                  />
                  {errors[`${certification.id}-dateReceived`] && (
                    <p className="text-red-500 text-sm mt-1">
                      {errors[`${certification.id}-dateReceived`]}
                    </p>
                  )}
                </div>
              </div>
            </Card>
          ))}

          <div className="flex justify-between">
            <Button type="button" variant="outline" onClick={addCertification}>
              <Plus className="h-4 w-4 mr-1" /> Add Certification
            </Button>
            <Button type="submit" disabled={certifications.length === 0}>
              Save Certifications
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
