"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { X, Plus } from "lucide-react";

interface Award {
  id: string;
  name: string;
  dateReceived: string;
}

interface Scholarship {
  id: string;
  name: string;
}

interface Education {
  id: string;
  department: string;
  course: string;
  gpa: string;
  yearGraduated: string;
  awards: Award[];
  scholarships: Scholarship[];
}

// Department options and their corresponding courses
const departmentOptions = {
  "Elementary": ["Elementary Education"],
  "High School": ["High School Education"],
  "Junior High": ["Junior High Education"],
  "Senior High": ["STEM", "ABM", "HUMSS", "GAS", "TVL"],
  "College of Graduate Studies": [
    "Master of Science in Agriculture",
    "Master of Science in Environmental Science",
    "Doctor of Philosophy in Agricultural Sciences",
    "Doctor of Philosophy in Environmental Science"
  ],
  "College of Agriculture": [
    "Bachelor of Science in Agriculture",
    "Bachelor of Science in Agricultural Economics",
    "Bachelor of Science in Food Technology"
  ],
  "Institute of Agricultural Science and Biosystems Engineering": [
    "Bachelor of Science in Agricultural and Biosystems Engineering"
  ],
  "College of Arts and Sciences": [
    "Bachelor of Arts in English",
    "Bachelor of Science in Biology",
    "Bachelor of Science in Chemistry",
    "Bachelor of Science in Mathematics"
  ],
  "College of Veterinary Medicine": ["Doctor of Veterinary Medicine"],
  "College of Agroforestry and Forestry": [
    "Bachelor of Science in Forestry",
    "Bachelor of Science in Agroforestry"
  ],
  "Institute of Agribusiness Management": [
    "Bachelor of Science in Agribusiness Management"
  ],
  "College of Education": [
    "Bachelor of Elementary Education",
    "Bachelor of Secondary Education",
    "Bachelor of Physical Education"
  ],
  "Institute of Environmental Studies": [
    "Bachelor of Science in Environmental Science"
  ],
  "College of Information Systems": [
    "Bachelor of Science in Information Systems - Business Analytics",
    "Bachelor of Science in Information Systems - Data Science",
    "Bachelor of Science in Information Systems - Enterprise Systems"
  ]
} as const;

export default function EducationForm() {
  const [educations, setEducations] = useState<Education[]>([]);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});

  const addEducation = () => {
    const newEducation: Education = {
      id: Date.now().toString(),
      department: "",
      course: "",
      gpa: "",
      yearGraduated: "",
      awards: [],
      scholarships: []
    };
    setEducations([...educations, newEducation]);
  };

  const removeEducation = (id: string) => {
    setEducations(educations.filter(edu => edu.id !== id));
  };

  const updateEducation = (id: string, field: keyof Education, value: any) => {
    setEducations(educations.map(edu => {
      if (edu.id === id) {
        // If updating department, reset course
        if (field === 'department') {
          return { ...edu, [field]: value, course: '' };
        }
        return { ...edu, [field]: value };
      }
      return edu;
    }));
    // Clear error when field is updated
    setErrors(prev => ({ ...prev, [`${id}-${field}`]: "" }));
  };

  const addAward = (educationId: string) => {
    setEducations(educations.map(edu => {
      if (edu.id === educationId) {
        return {
          ...edu,
          awards: [
            ...edu.awards,
            { id: Date.now().toString(), name: "", dateReceived: "" }
          ]
        };
      }
      return edu;
    }));
  };

  const updateAward = (educationId: string, awardId: string, field: keyof Award, value: string) => {
    setEducations(educations.map(edu => {
      if (edu.id === educationId) {
        return {
          ...edu,
          awards: edu.awards.map(award => {
            if (award.id === awardId) {
              return { ...award, [field]: value };
            }
            return award;
          })
        };
      }
      return edu;
    }));
  };

  const removeAward = (educationId: string, awardId: string) => {
    setEducations(educations.map(edu => {
      if (edu.id === educationId) {
        return {
          ...edu,
          awards: edu.awards.filter(award => award.id !== awardId)
        };
      }
      return edu;
    }));
  };

  const addScholarship = (educationId: string) => {
    setEducations(educations.map(edu => {
      if (edu.id === educationId) {
        return {
          ...edu,
          scholarships: [
            ...edu.scholarships,
            { id: Date.now().toString(), name: "" }
          ]
        };
      }
      return edu;
    }));
  };

  const updateScholarship = (educationId: string, scholarshipId: string, name: string) => {
    setEducations(educations.map(edu => {
      if (edu.id === educationId) {
        return {
          ...edu,
          scholarships: edu.scholarships.map(scholarship => {
            if (scholarship.id === scholarshipId) {
              return { ...scholarship, name };
            }
            return scholarship;
          })
        };
      }
      return edu;
    }));
  };

  const removeScholarship = (educationId: string, scholarshipId: string) => {
    setEducations(educations.map(edu => {
      if (edu.id === educationId) {
        return {
          ...edu,
          scholarships: edu.scholarships.filter(scholarship => scholarship.id !== scholarshipId)
        };
      }
      return edu;
    }));
  };

  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};
    let isValid = true;
    const currentYear = new Date().getFullYear();

    educations.forEach(edu => {
      if (!edu.department) {
        newErrors[`${edu.id}-department`] = "Department is required";
        isValid = false;
      }
      if (!edu.course) {
        newErrors[`${edu.id}-course`] = "Course/Strand/Major is required";
        isValid = false;
      }
      if (!edu.yearGraduated) {
        newErrors[`${edu.id}-yearGraduated`] = "Year graduated is required";
        isValid = false;
      } else if (parseInt(edu.yearGraduated) > currentYear) {
        newErrors[`${edu.id}-yearGraduated`] = "Year graduated cannot be in the future";
        isValid = false;
      }
    });

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      console.log("Saving education details:", educations);
      // Handle form submission
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Education</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {educations.map((education) => (
            <Card key={education.id} className="p-4 relative">
              <button
                type="button"
                onClick={() => removeEducation(education.id)}
                className="absolute top-2 right-2 text-gray-500 hover:text-red-500"
              >
                <X className="h-5 w-5" />
              </button>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                <div>
                  <Label htmlFor={`department-${education.id}`}>Department *</Label>
                  <Select
                    value={education.department}
                    onValueChange={(value) => updateEducation(education.id, "department", value)}
                  >
                    <SelectTrigger className={errors[`${education.id}-department`] ? "border-red-500" : ""}>
                      <SelectValue placeholder="Select department" />
                    </SelectTrigger>
                    <SelectContent>
                      {Object.keys(departmentOptions).map((dept) => (
                        <SelectItem key={dept} value={dept}>
                          {dept}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors[`${education.id}-department`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${education.id}-department`]}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`course-${education.id}`}>Course/Strand/Major *</Label>
                  <Select
                    value={education.course}
                    onValueChange={(value) => updateEducation(education.id, "course", value)}
                    disabled={!education.department}
                  >
                    <SelectTrigger className={errors[`${education.id}-course`] ? "border-red-500" : ""}>
                      <SelectValue placeholder="Select course" />
                    </SelectTrigger>
                    <SelectContent>
                      {education.department &&
                        departmentOptions[education.department as keyof typeof departmentOptions].map((course) => (
                          <SelectItem key={course} value={course}>
                            {course}
                          </SelectItem>
                        ))}
                    </SelectContent>
                  </Select>
                  {errors[`${education.id}-course`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${education.id}-course`]}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`gpa-${education.id}`}>GPA (Optional)</Label>
                  <Input
                    id={`gpa-${education.id}`}
                    value={education.gpa}
                    onChange={(e) => updateEducation(education.id, "gpa", e.target.value)}
                    type="number"
                    step="0.01"
                    min="0"
                    max="4"
                    placeholder="e.g., 3.50"
                  />
                </div>

                <div>
                  <Label htmlFor={`yearGraduated-${education.id}`}>Year Graduated *</Label>
                  <Input
                    id={`yearGraduated-${education.id}`}
                    value={education.yearGraduated}
                    onChange={(e) => updateEducation(education.id, "yearGraduated", e.target.value)}
                    type="number"
                    min="1900"
                    max={new Date().getFullYear()}
                    className={errors[`${education.id}-yearGraduated`] ? "border-red-500" : ""}
                  />
                  {errors[`${education.id}-yearGraduated`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${education.id}-yearGraduated`]}</p>
                  )}
                </div>
              </div>

              {/* Awards Section */}
              <div className="mb-4">
                <div className="flex items-center justify-between mb-2">
                  <Label>Awards</Label>
                  <Button type="button" variant="outline" size="sm" onClick={() => addAward(education.id)}>
                    <Plus className="h-4 w-4 mr-1" /> Add Award
                  </Button>
                </div>
                <div className="space-y-2">
                  {education.awards.map((award) => (
                    <div key={award.id} className="flex gap-2">
                      <Input
                        value={award.name}
                        onChange={(e) => updateAward(education.id, award.id, "name", e.target.value)}
                        placeholder="Award name"
                      />
                      <Input
                        type="date"
                        value={award.dateReceived}
                        onChange={(e) => updateAward(education.id, award.id, "dateReceived", e.target.value)}
                        max={new Date().toISOString().split("T")[0]}
                      />
                      <Button
                        type="button"
                        variant="outline"
                        size="icon"
                        onClick={() => removeAward(education.id, award.id)}
                      >
                        <X className="h-4 w-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </div>

              {/* Scholarships Section */}
              <div>
                <div className="flex items-center justify-between mb-2">
                  <Label>Scholarships</Label>
                  <Button type="button" variant="outline" size="sm" onClick={() => addScholarship(education.id)}>
                    <Plus className="h-4 w-4 mr-1" /> Add Scholarship
                  </Button>
                </div>
                <div className="space-y-2">
                  {education.scholarships.map((scholarship) => (
                    <div key={scholarship.id} className="flex gap-2">
                      <Input
                        value={scholarship.name}
                        onChange={(e) => updateScholarship(education.id, scholarship.id, e.target.value)}
                        placeholder="Scholarship name"
                      />
                      <Button
                        type="button"
                        variant="outline"
                        size="icon"
                        onClick={() => removeScholarship(education.id, scholarship.id)}
                      >
                        <X className="h-4 w-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </div>
            </Card>
          ))}

          <div className="flex justify-between">
            <Button type="button" variant="outline" onClick={addEducation}>
              <Plus className="h-4 w-4 mr-1" /> Add Education
            </Button>
            <Button type="submit" disabled={educations.length === 0}>
              Save Education Details
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
