"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Switch } from "@/components/ui/switch";
import { X, Plus } from "lucide-react";

interface Award {
  id: string;
  name: string;
  dateIssued: string;
}

interface Responsibility {
  id: string;
  description: string;
}

interface WorkExperience {
  id: string;
  companyName: string;
  department: string;
  designation: string;
  fromDate: string;
  toDate: string;
  isCurrentEmployer: boolean;
  responsibilities: Responsibility[];
  awards: Award[];
}

export default function WorkExperienceForm() {
  const [experiences, setExperiences] = useState<WorkExperience[]>([]);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});

  const addExperience = () => {
    const newExperience: WorkExperience = {
      id: Date.now().toString(),
      companyName: "",
      department: "",
      designation: "",
      fromDate: "",
      toDate: "",
      isCurrentEmployer: false,
      responsibilities: [],
      awards: []
    };
    setExperiences([...experiences, newExperience]);
  };

  const removeExperience = (id: string) => {
    setExperiences(experiences.filter(exp => exp.id !== id));
  };

  const updateExperience = (id: string, field: keyof WorkExperience, value: any) => {
    setExperiences(experiences.map(exp => {
      if (exp.id === id) {
        return { ...exp, [field]: value };
      }
      return exp;
    }));
    // Clear error when field is updated
    setErrors(prev => ({ ...prev, [`${id}-${field}`]: "" }));
  };

  const addResponsibility = (experienceId: string) => {
    setExperiences(experiences.map(exp => {
      if (exp.id === experienceId) {
        return {
          ...exp,
          responsibilities: [
            ...exp.responsibilities,
            { id: Date.now().toString(), description: "" }
          ]
        };
      }
      return exp;
    }));
  };

  const updateResponsibility = (experienceId: string, responsibilityId: string, description: string) => {
    setExperiences(experiences.map(exp => {
      if (exp.id === experienceId) {
        return {
          ...exp,
          responsibilities: exp.responsibilities.map(resp => {
            if (resp.id === responsibilityId) {
              return { ...resp, description };
            }
            return resp;
          })
        };
      }
      return exp;
    }));
  };

  const removeResponsibility = (experienceId: string, responsibilityId: string) => {
    setExperiences(experiences.map(exp => {
      if (exp.id === experienceId) {
        return {
          ...exp,
          responsibilities: exp.responsibilities.filter(resp => resp.id !== responsibilityId)
        };
      }
      return exp;
    }));
  };

  const addAward = (experienceId: string) => {
    setExperiences(experiences.map(exp => {
      if (exp.id === experienceId) {
        return {
          ...exp,
          awards: [
            ...exp.awards,
            { id: Date.now().toString(), name: "", dateIssued: "" }
          ]
        };
      }
      return exp;
    }));
  };

  const updateAward = (experienceId: string, awardId: string, field: keyof Award, value: string) => {
    setExperiences(experiences.map(exp => {
      if (exp.id === experienceId) {
        return {
          ...exp,
          awards: exp.awards.map(award => {
            if (award.id === awardId) {
              return { ...award, [field]: value };
            }
            return award;
          })
        };
      }
      return exp;
    }));
  };

  const removeAward = (experienceId: string, awardId: string) => {
    setExperiences(experiences.map(exp => {
      if (exp.id === experienceId) {
        return {
          ...exp,
          awards: exp.awards.filter(award => award.id !== awardId)
        };
      }
      return exp;
    }));
  };

  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};
    let isValid = true;

    experiences.forEach(exp => {
      if (!exp.companyName.trim()) {
        newErrors[`${exp.id}-companyName`] = "Company name is required";
        isValid = false;
      }
      if (!exp.department.trim()) {
        newErrors[`${exp.id}-department`] = "Department is required";
        isValid = false;
      }
      if (!exp.designation.trim()) {
        newErrors[`${exp.id}-designation`] = "Designation is required";
        isValid = false;
      }
      if (!exp.fromDate) {
        newErrors[`${exp.id}-fromDate`] = "From date is required";
        isValid = false;
      }
      if (!exp.isCurrentEmployer && !exp.toDate) {
        newErrors[`${exp.id}-toDate`] = "To date is required";
        isValid = false;
      }
    });

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      console.log("Saving work experiences:", experiences);
      // Handle form submission
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Work Experience</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {experiences.map((experience) => (
            <Card key={experience.id} className="p-4 relative">
              <button
                type="button"
                onClick={() => removeExperience(experience.id)}
                className="absolute top-2 right-2 text-gray-500 hover:text-red-500"
              >
                <X className="h-5 w-5" />
              </button>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                <div>
                  <Label htmlFor={`companyName-${experience.id}`}>Company Name *</Label>
                  <Input
                    id={`companyName-${experience.id}`}
                    value={experience.companyName}
                    onChange={(e) => updateExperience(experience.id, "companyName", e.target.value)}
                    className={errors[`${experience.id}-companyName`] ? "border-red-500" : ""}
                  />
                  {errors[`${experience.id}-companyName`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${experience.id}-companyName`]}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`department-${experience.id}`}>Department *</Label>
                  <Input
                    id={`department-${experience.id}`}
                    value={experience.department}
                    onChange={(e) => updateExperience(experience.id, "department", e.target.value)}
                    className={errors[`${experience.id}-department`] ? "border-red-500" : ""}
                  />
                  {errors[`${experience.id}-department`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${experience.id}-department`]}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`designation-${experience.id}`}>Designation/Position *</Label>
                  <Input
                    id={`designation-${experience.id}`}
                    value={experience.designation}
                    onChange={(e) => updateExperience(experience.id, "designation", e.target.value)}
                    className={errors[`${experience.id}-designation`] ? "border-red-500" : ""}
                  />
                  {errors[`${experience.id}-designation`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${experience.id}-designation`]}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor={`fromDate-${experience.id}`}>From Date *</Label>
                  <Input
                    id={`fromDate-${experience.id}`}
                    type="date"
                    value={experience.fromDate}
                    onChange={(e) => updateExperience(experience.id, "fromDate", e.target.value)}
                    max={new Date().toISOString().split("T")[0]}
                    className={errors[`${experience.id}-fromDate`] ? "border-red-500" : ""}
                  />
                  {errors[`${experience.id}-fromDate`] && (
                    <p className="text-red-500 text-sm mt-1">{errors[`${experience.id}-fromDate`]}</p>
                  )}
                </div>

                <div className="flex items-center gap-4">
                  <div className="flex-1">
                    <Label htmlFor={`toDate-${experience.id}`}>To Date</Label>
                    <Input
                      id={`toDate-${experience.id}`}
                      type="date"
                      value={experience.toDate}
                      onChange={(e) => updateExperience(experience.id, "toDate", e.target.value)}
                      disabled={experience.isCurrentEmployer}
                      min={experience.fromDate}
                      max={new Date().toISOString().split("T")[0]}
                      className={errors[`${experience.id}-toDate`] ? "border-red-500" : ""}
                    />
                    {errors[`${experience.id}-toDate`] && (
                      <p className="text-red-500 text-sm mt-1">{errors[`${experience.id}-toDate`]}</p>
                    )}
                  </div>
                  <div className="flex items-center gap-2">
                    <Switch
                      id={`currentEmployer-${experience.id}`}
                      checked={experience.isCurrentEmployer}
                      onCheckedChange={(checked) => updateExperience(experience.id, "isCurrentEmployer", checked)}
                    />
                    <Label htmlFor={`currentEmployer-${experience.id}`}>Current Employer</Label>
                  </div>
                </div>
              </div>

              {/* Responsibilities Section */}
              <div className="mb-4">
                <div className="flex items-center justify-between mb-2">
                  <Label>Key Responsibilities</Label>
                  <Button type="button" variant="outline" size="sm" onClick={() => addResponsibility(experience.id)}>
                    <Plus className="h-4 w-4 mr-1" /> Add Responsibility
                  </Button>
                </div>
                <div className="space-y-2">
                  {experience.responsibilities.map((resp) => (
                    <div key={resp.id} className="flex gap-2">
                      <Textarea
                        value={resp.description}
                        onChange={(e) => updateResponsibility(experience.id, resp.id, e.target.value)}
                        placeholder="Enter responsibility"
                      />
                      <Button
                        type="button"
                        variant="outline"
                        size="icon"
                        onClick={() => removeResponsibility(experience.id, resp.id)}
                      >
                        <X className="h-4 w-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </div>

              {/* Awards Section */}
              <div>
                <div className="flex items-center justify-between mb-2">
                  <Label>Awards</Label>
                  <Button type="button" variant="outline" size="sm" onClick={() => addAward(experience.id)}>
                    <Plus className="h-4 w-4 mr-1" /> Add Award
                  </Button>
                </div>
                <div className="space-y-2">
                  {experience.awards.map((award) => (
                    <div key={award.id} className="flex gap-2">
                      <Input
                        value={award.name}
                        onChange={(e) => updateAward(experience.id, award.id, "name", e.target.value)}
                        placeholder="Award name"
                      />
                      <Input
                        type="date"
                        value={award.dateIssued}
                        onChange={(e) => updateAward(experience.id, award.id, "dateIssued", e.target.value)}
                        max={new Date().toISOString().split("T")[0]}
                      />
                      <Button
                        type="button"
                        variant="outline"
                        size="icon"
                        onClick={() => removeAward(experience.id, award.id)}
                      >
                        <X className="h-4 w-4" />
                      </Button>
                    </div>
                  ))}
                </div>
              </div>
            </Card>
          ))}

          <div className="flex justify-between">
            <Button type="button" variant="outline" onClick={addExperience}>
              <Plus className="h-4 w-4 mr-1" /> Add Work Experience
            </Button>
            <Button type="submit" disabled={experiences.length === 0}>
              Save Work Experience
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
